import { useEffect, useRef } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import Modal2 from "../../Components/UI/Modal2/Modal2";
import { fetchDefaultBusiness } from "../../Store/actions";
import classes from "./BusinessDetailMenu.module.css";
import styled from "styled-components";

const Li = styled.li`
  a {
    opacity: 1;
    pointer-events: all;
  }

  ${(props) =>
    props.disabled &&
    `
    a {
     pointer-events: none;
    opacity: 0.5;
    }
  `}
`;

const BusinessDetailMenu = (props) => {
  const loadDataRef = useRef(() => {
    !props.selectedBusiness && props.fetchDefaultBusiness();
  });

  useEffect(() => {
    return loadDataRef.current();
  }, []);

  return (
    <section className={classes.BusinessDetailMenu}>
      <Modal2 title={props.match.params.business_name} />

      <main className={classes.BusinessDetailMenu_Main}>
        <ul>
          <Li
            disabled={
              props.selectedBusiness && props.selectedBusiness.role !== "admin"
            }
          >
            <Link
              to={`/business/${props.match.params.businessId}/account-detail`}
            >
              Basic Account Detail
            </Link>
          </Li>
          {/* <Li
            disabled={
              props.selectedBusiness && props.selectedBusiness.role !== "owner"
            }
          >
            <Link
              to={`/business/${props.match.params.businessId}/social-profiles`}
            >
              Social Profiles
            </Link>
          </Li> */}
          {/* <Li
            disabled={
              props.selectedBusiness && props.selectedBusiness.role !== "owner"
            }
          >
            <Link to={`/user/payments`}>Payments and Renewals</Link>
          </Li> */}
          <Li
            disabled={
              props.selectedBusiness && props.selectedBusiness.role !== "admin"
            }
          >
            <Link
              to={`/account/system-schedule/${props.match.params.businessId}`}
            >
              System Schedule
            </Link>
          </Li>
          <Li
            disabled={
              props.selectedBusiness && props.selectedBusiness.role !== "admin"
            }
          >
            <Link to={`/account/lms-rules/${props.match.params.businessId}`}>
              Whatsapp Rules
            </Link>
          </Li>
          <li>
            <a href={`/business/${props.match.params.businessId}/team-members`}>
              Team Members
            </a>
          </li>
        </ul>
      </main>
    </section>
  );
};

const mapStateToProps = (state) => {
  return {
    selectedBusiness: state.business.selectedBusiness,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    fetchDefaultBusiness: () => dispatch(fetchDefaultBusiness()),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(BusinessDetailMenu);
